<template>
  <div class='dm_headerBar_topMenu'>
      <el-row class='common_width'>
            <el-col :span='6'>
                <i class="el-icon-location-outline"></i>
                
            </el-col>
            <el-col :span='18'>
                <template v-if='uname && token'>
                    <a>{{ uname }}</a>
                    <a @click="$store.dispatch('postLogoutData')"></a>
                </template>
                <template v-else>
                    <router-link to='/login'></router-link>
                    <router-link to='register'></router-link>
                </template>
                <router-link to='/myOrder'></router-link>
                <router-link to='/myCollection'></router-link>
                <router-link to='/userCenter'></router-link>
            </el-col>
      </el-row>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    data() {
        return {}
    },
    computed: {
        ...mapState({
            uname: state => state.uname,
            token: state => state.token
        })
    }
}
</script>

<style lang="less">
    @import "./index.less";
</style>